.clock {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#time-box {
  width: 300px;
  color: #183c52;
  background-color: #c4c4c4;
  border-radius: 20px;
  justify-content: center;
  gap: 20px;
  padding: 10px;
  font-size: 40px;
  font-weight: 700;
  display: inline-flex;
}

#ampm {
  font-size: 20px;
  font-weight: 700;
}

.lines-box {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.time-text {
  color: #002c47;
  margin-top: 10px;
  font-family: arial;
  font-size: 18px;
  font-weight: 700;
}

.time-line {
  width: 280px;
  height: 20px;
  background-color: #00f;
  border: 2px solid rgba(0, 0, 0, .31);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.time-slider {
  width: 20px;
  height: 20px;
  background-color: #d9ff00;
  border-radius: 50%;
  transition-duration: 1s;
  position: absolute;
  top: 0;
  left: -20px;
}

